import { BpmnModel } from "./BpmnModel";
import { BpmnModelElement } from "./BpmnModelElement";
import { BpmnModelHelper } from "./BpmnModelHelper";
import { IClrTypeElement } from "./IClrTypeElement";

export abstract class ExtensionElement extends BpmnModelElement implements IClrTypeElement {
    constructor(model: BpmnModel) {
        super(model);
    }

    abstract ClrTypeID: string;
    abstract NewInstance(): ExtensionElement;

    MexTag: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.MexTag = null;
    }

    ConvertToJsonObject(): any {
        let obj: any = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "ClrTypeID", this.ClrTypeID);
        BpmnModelHelper.SetAttributeValue(obj, "MexTag", this.MexTag);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.MexTag = obj.MexTag;
    }
}
